@import '../styles/mixin.scss';

.col-4 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 100%;
    text-align: center;
}

.col-8 {
    width: 60%;
    height: 100%;

    @include mobile {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
}

.cards-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
    gap: 10px;

    @include mobile {
        grid-template-columns: 1fr;
        overflow-x: auto;
        scrollbar-width: none;
        -ms-overflow-style: none;

        &::-webkit-scrollbar {
            display: none;
        }
    }
}

.cards-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;

    @include mobile {
        flex-direction: row;
        overflow: auto;

        ::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }
}

.section-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 50px;
    color: #343C6A;
}