/* Mixin */
/* Set các giá trị với màn hình tương ứng */

$mobile-width: 480px;
$tablet-width: 768px;
$desktop-width: 1024px;

@mixin mobile {
    @media (max-width: #{$desktop-width - 1px}) {
        @content;
    }
}

// @mixin tablet {
//     @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
//         @content;
//     }
// }

@mixin desktop {
    @media (min-width: #{$desktop-width}) {
        @content;
    }
}